import Vue from 'vue'
import Router from 'vue-router'

import index from '@/components/index'
import userJoin from '@/components/userJoin'
import login from '@/components/login'
import code from '@/components/code'
import setPass from '@/components/setPass'

import home from '@/components/home'
import addTicket from '@/components/addTicket'
import recharge from '@/components/recharge'
import recharge2 from '@/components/recharge2'
import coupon from '@/components/coupon'
import useCoupon from '@/components/useCoupon'
import plan from '@/components/plan'
import pay from '@/components/pay'
import payCallback from '@/components/payCallback'
import userSet from '@/components/userSet'
import addressList from '@/components/addressList'
import addAddress from '@/components/addAddress'
import editAddress from '@/components/editAddress'
import order from '@/components/order'
import myCard from '@/components/myCard'
import invoiceLog from '@/components/invoiceLog'
import orderDetail from '@/components/orderDetail'
import msgCenter from '@/components/msgCenter'
import noticeList from '@/components/noticeList'
import msgList from '@/components/msgList'
import page from '@/components/page'

import find from '@/components/find'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  base: Vue.prototype.base,
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      meta: {title: '油大大'}
    },
    {
      path: '/userJoin',
      name: 'userJoin',
      component: userJoin,
      meta: {title: '登录/注册'}
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {title: '登录'}
    },
    {
      path: '/code',
      name: 'code',
      component: code,
      meta: {title: '短信验证码'}
    },
    {
      path: '/setPass',
      name: 'setPass',
      component: setPass,
      meta: {title: '设置密码'}
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      meta: {
        title: '个人中心',
        requireAuth: true
      }
    },
    {
      path: '/addTicket',
      name: 'addTicket',
      component: addTicket,
      meta: {title: '添加油卡'}
    },
    {
      path: '/recharge',
      name: 'recharge',
      component: recharge,
      meta: {title: '套餐充值'}
    },
    {
      path: '/recharge2',
      name: 'recharge2',
      component: recharge2,
      meta: {title: '即时充值'}
    },
    {
      path: '/coupon',
      name: 'coupon',
      component: coupon,
      meta: {title: '我的券包'}
    },
    {
      path: '/useCoupon',
      name: 'useCoupon',
      component: useCoupon,
      meta: {title: '使用红包'}
    },
    {
      path: '/plan',
      name: 'plan',
      component: plan,
      meta: {title: '加油计划'}
    },
    {
      path: '/pay',
      name: 'pay',
      component: pay,
      meta: {title: '收银台'}
    },
    {
      path: '/payCallback',
      name: 'payCallback',
      component: payCallback,
      meta: {title: '收银台'}
    },
    {
      path: '/userSet',
      name: 'userSet',
      component: userSet,
      meta: {title: '个人信息'}
    },
    {
      path: '/addressList',
      name: 'addressList',
      component: addressList,
      meta: {title: '地址管理'}
    },
    {
      path: '/addAddress',
      name: 'addAddress',
      component: addAddress,
      meta: {title: '添加新地址'}
    },
    {
      path: '/editAddress',
      name: 'editAddress',
      component: editAddress,
      meta: {title: '编辑地址'}
    },
    {
      path: '/order',
      name: 'order',
      component: order,
      meta: {title: '我的订单'}
    },
    {
      path: '/orderDetail',
      name: 'orderDetail',
      component: orderDetail,
      meta: {title: '订单详情'}
    },
    {
      path: '/myCard',
      name: 'myCard',
      component: myCard,
      meta: {title: '油卡管理'}
    },
    {
      path: '/invoiceLog',
      name: 'invoiceLog',
      component: invoiceLog,
      meta: {title: '发票记录'}
    },
    {
      path: '/msgCenter',
      name: 'msgCenter',
      component: msgCenter,
      meta: {title: '消息中心'}
    },
    {
      path: '/noticeList',
      name: 'noticeList',
      component: noticeList,
      meta: {title: '官方公告'}
    },
    {
      path: '/msgList',
      name: 'msgList',
      component: msgList,
      meta: {title: '站内信'}
    },
    {
      path: '/page',
      name: 'page',
      component: page,
      meta: {title: '公告详情'}
    },
    {
      path: '/find',
      name: 'find',
      component: find,
      meta: {title: '发现'}
    }
  ]
})
router.beforeEach((to, from, next) => {
  if (to.matched.some(res => res.meta.requireAuth)) {
    let token = localStorage.getItem('hykToken')
    if (token === null || token === '') {
      next({
        path: '/userJoin',
        query: {redirect: to.fullPath}
      })
    } else {
      next()
    }
  } else {
    next()
  }
})
export default router



