<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll"
      :options="options"
      :data="items"
      @pulling-down="onPullingDown"
      @pulling-up="onPullingUp"
      class="page_full bodyBg">
      <div class="noDataTip coupon" v-if="items.length===0 && isLoadDom">
        <dl>
          <dt></dt>
          <dd>暂无红包</dd>
        </dl>
      </div>
      <div class="coupon_list">
        <div class="coupon_box" v-for="(data,index) in items" :key="index"
             :class="data.status!=='0' && data.status!=='3'?data.status==='1'? 'used':'over_time':''">
          <div class="coupon_info">
            <dl>
              <dt><span>￥</span>{{data.money}}</dt>
              <dd>
                <h2>{{data.title}}</h2>
                <p v-if="data.redType==='0'">仅限套餐充值使用</p>
                <p v-else-if="data.redType==='1'">仅限即时充值使用</p>
                <p v-else>无限制</p>
              </dd>
            </dl>
            <div class="use" v-if="data.status==='0'">
              <router-link :to="data.redType === '0'?'/recharge':'/recharge2'">去使用</router-link>
            </div>
            <div class="flag" v-else>
            </div>
          </div>
          <div class="coupon_time">
            过期时间：{{data.overTimeStr}}
          </div>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  import {postRequest} from '../axios'
  export default {
    name: 'coupon',
    data () {
      return {
        items: [],
        pageNum: 2,
        isLoadDom: false,
        token: localStorage.getItem('hykToken'),
        options: {
          pullDownRefresh: {
            threshold: 80,
            txt: '刷新成功'
          },
          pullUpLoad: {
            threshold: 80,
            txt: {
              more: '加载更多',
              noMore: '没有更多了'
            }
          },
          scrollbar: {
            fade: true
          }
        }
      }
    },
    mounted () {
      this.loadPage(1, 1)
    },
    methods: {
      loadPage (pageNum, type) {
        let _this = this
        postRequest(this.HOST + '/mine/allRedPackage', {
          token: _this.token,
          currPage: pageNum
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          let list = data.list
          _this.isLoadDom = true
          list.forEach(function (item, i) {
            let overTimeStr = item.overTimeStr
            item.overTimeStr = _this.formatDate(overTimeStr, 'yyyy-mm-dd hh:MM:ss')
          })
          if (type === 1) {
            _this.items = list
            _this.pageNum = 2
          } else {
            if (list.length > 0) {
              _this.items = _this.items.concat(list)
              _this.pageNum = _this.pageNum + 1
            }
          }
          setTimeout(function () {
            _this.$refs.scroll.forceUpdate()
          }, 300)
        })
      },
      onPullingDown () {
        this.loadPage(1, 1)
      },
      onPullingUp () {
        let _this = this
        setTimeout(function () {
          _this.loadPage(_this.pageNum, 2)
        }, 500)
      },
      formatDate (now, fmt) {
        let date = new Date(now * 1000)
        let o = {
          'M+': date.getMonth() + 1, // 月份
          'd+': date.getDate(), // 日
          'h+': date.getHours(), // 小时
          'm+': date.getMinutes(), // 分
          's+': date.getSeconds() // 秒
        }
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
        for (let k in o) {
          if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
        }
        return fmt
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    }
  }
</script>
<style lang="less">
  .coupon_list {
    width: 100%;
    height: auto;
    padding: 13px 13px 0 13px !important;
    .coupon_box {
      width: 100%;
      height: auto;
      margin-bottom: 10px;
      .coupon_info {
        width: 100%;
        height: 82px;
        padding: 0 20px;
        background: url("../../static/images/coupon_top_bg.png") no-repeat center center;
        background-size: cover;
        position: relative;
        overflow: hidden;
        .use {
          width: 60px;
          height: 25px;
          line-height: 24px;
          border-radius: 13px;
          border: solid 1px #fff;
          position: absolute;
          text-align: center;
          color: #fff;
          font-size: 14px;
          right: 20px;
          top: 50%;
          margin-top: -13px;
          a {
            color: #fff;
          }
        }
        .flag {
          width: 50px;
          height: 50px;
          position: absolute;
          top: 50%;
          margin-top: -26px;
          right: 20px;
        }
        dl {
          dt {
            color: #fff;
            font-size: 40px;
            float: left;
            line-height: 82px;
            span {
              font-size: 15px;
            }
          }
          dd {
            float: left;
            color: #fff;
            text-align: left;
            padding: 20px;
            h2 {
              font-size: 15px;
              font-weight: normal;
              line-height: 1;
              padding-bottom: 15px;
            }
            p {
              font-size: 12px;
              line-height: 1;
            }
          }
        }
      }
      .coupon_time {
        width: 100%;
        height: 43px;
        background: url("../../static/images/coupon_bottom_bg.png") no-repeat center center;
        background-size: cover;
        overflow: hidden;
        line-height: 43px;
        color: #999999;
        font-size: 12px;
        text-align: left;
        padding: 0 20px;
      }
    }
    .used, .over_time {
      .coupon_info {
        background: url("../../static/images/coupon_disabled.png") no-repeat center center;
        background-size: cover;
      }
      .coupon_time {
        background: url("../../static/images/coupon_disabled_bg.png") no-repeat center center;
        background-size: cover;
      }
    }
    .used {
      .flag {
        background: url("../../static/images/userd.png") no-repeat center center;
        background-size: contain;
      }
    }
    .over_time {
      .flag {
        background: url("../../static/images/overtime.png") no-repeat center center;
        background-size: contain;
      }
    }
  }
</style>



