<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll" :options="options" class="page_full">
      <div class="page detail">
        <div class="page_title">
          <h2>关于放假通知</h2>
          <p>2018-11-11 09:20:00</p>
        </div>
        <div class="content">
          <p>国民党“立委”柯志恩12日爆料台北故宫正评估2020年封馆整修3年后，岛内立即出现强烈反应。旅游业者批评称，台北故宫一旦闭馆，游客恐怕连台湾都不想来，将严重影响观光产值。面对反弹声浪，台当局政策急转弯。据台湾《联合报》14日报道，台北故宫院长陈其南13日改口称，根据去年“行政院”核定的“新故宫计划”，内容未提及北院整修需要闭馆。“行政院”发言人称，“北院整修期间不闭馆，南院持续扩大充实展览内容”。“政务委员”张景森13日晚间针对反对声音称，“重要的国宝移到南院去，天经地义，除非你是国民党一样的心态，故意把南院变成蚊子院”。东森新闻网披露称，今年1月“行政院第3584次院会决议”中明确记载，挑选台北故宫一定数量重要典藏品，限定3年内仅在南院展览，正是张景森给“行政院长”赖清德出的主意。</p>
          <p>张景森的论调被网民一面倒地骂翻。有人称，“原来张景森是太上皇，他说了算，他的天经地义是大家的天方夜谭”“南院连泼漆都防不了，拿国宝开玩笑吗？民进党一天到晚去中国化，要赚钱时又动起中华文物的脑筋”。</p>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: 'page',
    data () {
      return {
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        }
      }
    }
  }
</script>
<style lang="less">
  .detail{
     color:#333;
     padding:20px 15px;
     .page_title{
        width:100%;
        padding-bottom:20px;
        h2{
           font-size:16px;
           padding-bottom:5px;
        }
        p{
           font-size:11px;
           color:#999999;
           text-align:center;
        }
     }
     .content{
        width:100%;
        padding-bottom:30px;
        p{
           text-align:left;
           text-indent:2em;
           line-height:1.5em;
           font-size:14px;
           color:#666666;
           padding:2px 0;
        }
     }
  }
</style>



