<template>
  <div class="content_panel reg_panel">
    <cube-scroll
      ref="scroll"
      @pulling-down="onPullingDown"
      @pulling-up="onPullingUp"
      :options="options"
      :data="items"
      class="page_scroll">
      <div class="page find_page">
        <div class="find_box" v-for="(data,index) in items" :style="{backgroundImage:`url(${imgSrc})`}">
        </div>
      </div>
    </cube-scroll>
    <footer-bar :sy="1"></footer-bar>
  </div>
</template>

<script>
  import {postRequest} from '../axios'
  import footerBar from './footerBar'

  export default {
    name: 'home',
    data () {
      return {
        imgSrc: '../../static/images/tempImg.png',
        items: [],
        pageNum: 2,
        isLoadDom: false,
        token: localStorage.getItem('hykToken'),
        options: {
          pullDownRefresh: {
            threshold: 60,
            txt: '刷新成功'
          },
          pullUpLoad: {
            threshold: 60,
            txt: {
              more: '加载更能',
              noMore: '— 更多内容，敬请期待 —'
            }
          },
          scrollbar: {
            fade: true
          }
        }
      }
    },
    mounted () {
      this.loadPage(1, 1)
    },
    methods: {
      loadPage (pageNum, type) {
        let _this = this
        postRequest(this.HOST + '/mine/allRedPackage', {
          token: _this.token,
          currPage: pageNum
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          let list = data.list
          _this.isLoadDom = true
          if (type === 1) {
            _this.items = list
            _this.pageNum = 2
          } else {
            if (list.length > 0) {
              _this.items = _this.items.concat(list)
              _this.pageNum = _this.pageNum + 1
            }
          }
          setTimeout(function () {
            _this.$refs.scroll.forceUpdate()
          }, 300)
        })
      },
      onPullingDown () {
        this.loadPage(1, 1)
      },
      onPullingUp () {
        let _this = this
        setTimeout(function () {
          _this.loadPage(_this.pageNum, 2)
        }, 500)
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    },
    components: {
      footerBar
    }
  }
</script>

<style lang="less">
  .find_page {
    width: 100%;
    height: auto;
    padding: 15px 15px 0 15px !important;
    color: #333;
    .find_box {
      width: 100%;
      height: 150px;
      border-radius: 3px;
      overflow: hidden;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
      margin-bottom: 14px;
    }
  }
</style>



