<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll" :options="options" class="page_full">
      <div class="page pay_page">
        <div class="pay_status">
          <dl>
            <dt>
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#duigou"></use>
              </svg>
            </dt>
            <dd>订单已支付</dd>
          </dl>
          <p>将在2-48小时内到帐</p>
          <div class="submit-button">
            <cube-button @click="handleAgain">继续充值</cube-button>
            <router-link tag="p" to="/order">查看订单</router-link>
          </div>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: 'pay-callback',
    data () {
      return {
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        }
      }
    },
    methods: {
      handleAgain () {
        this.$router.replace('/recharge')
      }
    }
  }
</script>
<style lang="less" src="../assets/pay.less"></style>



