<template>
  <div class="content_panel reg_panel">
    <cube-scroll
      ref="scroll"
      :options="options"
      class="page_scroll bodyBg">
      <div class="page user_page">
        <div class="top_bar">
          <router-link tag="div" class="msg" to="/msgCenter">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#xiaoxizhongxin"></use>
            </svg>
          </router-link>
          <router-link tag="dl" to="/userSet">
            <dt :style="{backgroundImage:`url(${defaultImg})`}"></dt>
            <dd>{{phoneTxt}}</dd>
            <div class="clear"></div>
          </router-link>
        </div>
        <div class="ticket_banner">
          <div class="add_ticket" :class="{show:items.length<1}">
            <router-link to="/addTicket">
              <p><i>+</i>添加油卡</p>
            </router-link>
          </div>
          <div id="ticketList" v-if="items.length>0">
            <div class="swiper-container swiper-home">
              <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(data,index) in items" :key="index">
                  <div class="card_box">
                    <p>{{data.oilType==='1'?'中国石油':'中国石化'}}</p>
                    <dl>
                      <dt>{{data.name}}</dt>
                      <dd>{{data.oliCardNo}}</dd>
                    </dl>
                    <ul>
                      <li>本月应到账：{{data.ydzMoney}}元</li>
                      <li>累计充值金额：{{data.money}}元</li>
                      <div class="clear"></div>
                    </ul>
                    <svg class="icon" aria-hidden="true">
                      <use :xlink:href="data.oilType==='1'?'#zhongshiyou':'#zhongshihua'"></use>
                    </svg>
                  </div>
                </div>
              </div>
              <div class="swiper-pagination"></div>
            </div>
          </div>
          <div class="shadow">
          </div>
        </div>
        <div class="tab_menu">
          <ul>
            <router-link tag="li" to="/plan">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#jiayoujihua"></use>
              </svg>
              加油计划
            </router-link>
            <router-link tag="li" to="/coupon">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#youhuiquan"></use>
              </svg>
              优惠券
            </router-link>
          </ul>
          <div class="clear"></div>
        </div>
        <div class="tool_list">
          <router-link tag="dl" to="/order" class="border_right border-bottom">
            <dt>
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#wodedingdan"></use>
              </svg>
            </dt>
            <dd>我的订单</dd>
          </router-link>
          <router-link tag="dl" to="/myCard" class="border-bottom">
            <dt>
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#qiapianguanli"></use>
              </svg>
            </dt>
            <dd>油卡管理</dd>
          </router-link>
          <dl class="border_right">
            <dt>
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#wentijieda"></use>
              </svg>
            </dt>
            <dd>问题解答</dd>
          </dl>
          <dl>
            <dt>
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#kefuzhongxin"></use>
              </svg>
            </dt>
            <dd>客服中心</dd>
          </dl>
          <div class="clear"></div>
        </div>
      </div>
    </cube-scroll>
    <footer-bar :sy="2"></footer-bar>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  import footerBar from './footerBar'
  import {postRequest} from '../axios'

  export default {
    name: 'home',
    data () {
      return {
        swiper: null,
        items: [],
        phone: localStorage.getItem('hykPhone'),
        phoneTxt: '',
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        },
        token: localStorage.getItem('hykToken'),
        defaultImg: '../../static/images/defaultLitpic.png'
      }
    },
    mounted () {
      let _this = this
      _this.phoneTxt = _this.phone.substr(0, 3) + '****' + _this.phone.substr(7)
      _this.loadPage()
    },
    watch: {
      items (n) {
        if (n.length <= 1) {
          return false
        }
        this.$nextTick(function () {
          this.initSwiper()
        })
      }
    },
    methods: {
      loadPage () {
        let _this = this
        postRequest(this.HOST + '/mine/oilCard', {
          token: _this.token
        }).then(resp => {
          let data = resp.data
          let list = data.list
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          list.forEach(function (item, i) {
            let oliCard = item.oliCardNo
            item.oliCardNo = oliCard.replace(/(.{4})/g, '$1 ')
          })
          _this.items = list
        })
      },
      initSwiper () {
        this.mySwiper = new Swiper('.swiper-home', {
          watchSlidesProgress: true,
          slidesPerView: 'auto',
          centeredSlides: true,
          loop: true,
          onProgress: function (swiper, progress) {
            for (let i = 0; i < swiper.slides.length; i++) {
              let slide = swiper.slides.eq(i)
              let slideProgress = swiper.slides[i].progress
              let modify = 1
              if (Math.abs(slideProgress) > 1) {
                modify = (Math.abs(slideProgress) - 1) * 0.3 + 1
              }
              let translate = slideProgress * modify * 3 + 'px'
              let scale = 1 - Math.abs(slideProgress) / 7
              let zIndex = 999 - Math.abs(Math.round(10 * slideProgress))
              slide.transform('translateX(' + translate + ') scale(' + scale + ')')
              slide.css('zIndex', zIndex)
              slide.css('opacity', 1)
              if (Math.abs(slideProgress) > 2) {
                slide.css('opacity', 0)
              }
            }
          },
          onSetTransition: function (swiper, transition) {
            for (var i = 0; i < swiper.slides.length; i++) {
              var slide = swiper.slides.eq(i)
              slide.transition(transition)
            }
          }
        })
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    },
    components: {
      footerBar
    }
  }
</script>

<style lang="less">
  .user_page {
    color: #333;
    padding-bottom: 15px !important;
    svg {
      width: 100%;
      height: 100%;
    }
    .top_bar {
      width: 100%;
      height: auto;
      padding: 15px 15px 25px 15px;
      background: #fff;
      dl {
        dt {
          width: 35px;
          height: 35px;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center center;
          border-radius: 50%;
          overflow: hidden;
          float: left;
        }
        dd {
          float: left;
          line-height: 35px;
          font-size: 18px;
          padding-left: 15px;
          color: #333;
        }
      }
      .msg {
        width: 23px;
        height: 35px;
        float: right;
        svg {
          width: 100%;
          height: 100%;
          fill: #fff;
        }
      }
    }
    .ticket_banner {
      width: 100%;
      background: #fff;
      height: 137px;
      overflow: hidden;
      position: relative;
      padding-top: 5px;
      #ticketList {
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 99;
        .swiper-container {
          width: 100%;
          height: 100%;
          .swiper-slide.swiper-slide-prev {
            transform-origin: 100% bottom !important;
          }
          .swiper-slide.swiper-slide-next {
            transform-origin: 0 bottom !important;
          }
          .swiper-slide.swiper-slide-active {
            transform-origin: 100% bottom !important;
          }
          .swiper-slide {
            width: 84%;
            position: relative;
            margin:0 auto;
            .card_box {
              width: 7.8rem;
              height: 100%;
              border-radius: 5px 5px 0 0;
              margin: 0 auto;
              position: relative;
              background: -webkit-linear-gradient(left top, #FF8D3D, #FF3548);
              background: -o-linear-gradient(bottom right, #FF8D3D, #FF3548);
              background: -moz-linear-gradient(bottom right, #FF8D3D, #FF3548);
              background: linear-gradient(to bottom right, #FF8D3D, #FF3548);
              box-shadow: 0 1px 8px rgba(213, 43, 43, 0.5);
              overflow: hidden;
              p {
                font-size: 14px;
                color: rgba(255, 255, 255, 0.4);
                position: absolute;
                text-align: right;
                width: 100%;
                padding-right: 10px;
                top: 20px;
              }
              .icon {
                width: 100px;
                height: 100px;
                position: absolute;
                bottom: -20px;
                right: -10px;
                fill: rgba(255, 255, 255, 0.3)
              }
              ul {
                width: 100%;
                height: auto;
                background: rgba(0, 0, 0, 0.1);
                position: absolute;
                left: 0;
                bottom: 0;
                z-index: 9;
                li {
                  float: left;
                  width: 50%;
                  font-size: 13px;
                  color: #fff;
                  padding: 12px 0;
                  border-right: solid 1px rgba(255, 255, 255, 0.05)
                }
              }
              dl {
                width: 100%;
                padding: 30px 12px 20px 12px;
                dt {
                  color: #fff;
                  text-align: left;
                  font-size: 18px;
                  line-height: 1;
                  padding-bottom: 18px;
                  letter-spacing: 2px;
                }
                dd {
                  text-align: left;
                  color: #fff;
                  letter-spacing: 1px;
                  font-size: 15px;
                }
              }
            }
          }
        }
      }
      .shadow {
        height: 2px;
        position: absolute;
        width: 100%;
        bottom: -2px;
        box-shadow: 0 0 5px #ccc;
      }
      .add_ticket.show {
        display: block;
      }
      .add_ticket {
        width: 7.8rem;
        height: 100%;
        display: none;
        position: relative;
        z-index: 999;
        margin: 0 auto;
        border-radius: 5px 5px 0 0;
        background: -webkit-linear-gradient(left top, #FF8D3D, #FF3548);
        background: -o-linear-gradient(bottom right, #FF8D3D, #FF3548);
        background: -moz-linear-gradient(bottom right, #FF8D3D, #FF3548);
        background: linear-gradient(to bottom right, #FF8D3D, #FF3548);
        box-shadow: 0 5px 10px #d52b2b;
        p {
          text-align: center;
          display: inline-block;
          color: #fff;
          font-size: 16px;
          line-height: 132px;
          letter-spacing: 2px;
          i {
            font-style: normal;
            margin-right: 5px;
            font-size: 24px;
            vertical-align: -1px;
          }
        }
      }
    }
    .tab_menu {
      width: 100%;
      height: 70px;
      box-shadow: 0 5px 15px #E8E8E8;
      border-top: solid 1px #F5F5F5;
      background: #fff;
      position: relative;
      z-index: 99;
      margin-bottom: 15px;
      ul {
        width: 100%;
        height: 70px;
        li {
          width: 50%;
          float: left;
          line-height: 70px;
          svg {
            width: 19px;
            height: 19px;
            display: inline-block;
            vertical-align: -4px;
            margin-right: 5px;
          }
          font-size: 14px;
        }
        li:first-child {
          border-right: solid 1px #F0F0F0;
        }
      }
    }
    .tool_list {
      width: 100%;
      height: auto;
      box-shadow: 0 1px 6px #E8E8E8;
      background: #fff;
      dl {
        width: 50%;
        float: left;
        padding: 28px 0;
        dt {
          width: 27px;
          height: 27px;
          margin: 0 auto;
        }
        dd {
          line-height: 1;
          padding-top: 14px;
          text-align: center;
          color: #333;
          font-size: 14px;
        }
      }
      .border_right {
        border-right: solid 1px #F0F0F0;
      }
      .border-bottom {
        border-bottom: solid 1px #F0F0F0;
      }
    }
  }
</style>
<style lang="css" src="swiper/dist/css/swiper.min.css" scoped></style>



