<template>
  <div class="content_panel">
    <div class="timePicket">
      <cube-scroll
        ref="dateScroll"
        direction="horizontal"
        class="outer-horizontal-scroll">
        <ul class="list-wrapper">
          <li class="list-item" v-for="(data,index) in datePicker" :key="index" ref="listGroup" :class="{active:nowYear==data.year && nowMonth==data.month}">
            <dl>
              <dt>{{data.month}}</dt>
              <dd>{{data.year}}</dd>
            </dl>
          </li>
        </ul>
      </cube-scroll>
    </div>
    <cube-scroll
      ref="scroll"
      :options="options"
      class="page_topbar bodyBg">
      <div class="plan_list">
        <div class="title">
          <i></i>2018年11月
        </div>
        <div class="sum">
          <p><span>应加总额：<label>2000.00</label></span>已加总额：<label>2000.00</label></p>
        </div>
        <div class="plan_box">
          <dl>
            <dt>
              <h2>14</h2>
              <p>2018.11</p>
            </dt>
            <dd>
              <h2>1000<label>( 1/6 )</label></h2>
              <p><label>3308811992100323345</label>姜南平</p>
            </dd>
            <div class="clear"></div>
          </dl>
        </div>
        <div class="plan_box">
          <dl>
            <dt>
              <h2>14</h2>
              <p>2018.11</p>
            </dt>
            <dd>
              <h2>1000<label>( 即时充值 )</label></h2>
              <p><label>3308811992100323345</label>姜南平</p>
            </dd>
            <div class="clear"></div>
          </dl>
        </div>
        <div class="sum">
          <p><span>待加总额：<label>2000.00</label></span>已加总额：<label>2000.00</label></p>
        </div>
        <div class="plan_box">
          <dl>
            <dt>
              <h2>14</h2>
              <p>2018.11</p>
            </dt>
            <dd>
              <h2>1000<label>(1/6)</label></h2>
              <p><label>3308811992100323345</label>姜南平</p>
            </dd>
            <div class="clear"></div>
          </dl>
        </div>
        <div class="plan_box">
          <dl>
            <dt>
              <h2>14</h2>
              <p>2018.11</p>
            </dt>
            <dd>
              <h2>1000<label>(1/6)</label></h2>
              <p><label>3308811992100323345</label>姜南平</p>
            </dd>
            <div class="clear"></div>
          </dl>
        </div>
        <div class="plan_box">
          <dl>
            <dt>
              <h2>14</h2>
              <p>2018.11</p>
            </dt>
            <dd>
              <h2>1000<label>(1/6)</label></h2>
              <p><label>3308811992100323345</label>姜南平</p>
            </dd>
            <div class="clear"></div>
          </dl>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: 'plan',
    data () {
      return {
        startX: 200,
        datePicker: [],
        nowYear: '',
        nowMonth: '',
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        }
      }
    },
    mounted () {
      this.getDateList('201801', '202001')
      this.getNowDate()
      // this.$refs.dateScroll.scrollTo(
      //   -200,
      //   0,
      //   200
      // )
      this._scrollTo(2)
    },
    methods: {
      _scrollTo (index) {
        console.log(this.$refs.listGroup)
       // this.$refs.dateScroll.scrollToElement(this.$refs.listGroup[index], 0)
      },
      getNowDate () {
        let now = new Date()
        this.nowYear = now.getFullYear()
        this.nowMonth = now.getMonth() + 1
      },
      getDateList (startDateStr, endDateStr) {
        let _this = this
        let startYear = startDateStr.substr(0, 4) // 开始时间的年份
        let startMonth = startDateStr.substr(4, 2)// 开始时间的月份
        let endYear = endDateStr.substr(0, 4)// 结束时间的年份
        let endMonth = endDateStr.substr(4, 2)// 结束时间的月份
        let startDate = new Date(startYear, startMonth)
        let years = endYear - startYear - 1
        let count = 0// 计算相隔月份
        if (years >= 0) {
          count = years * 12
          count += (12 - parseInt(startMonth))
          count += parseInt(endMonth)
        } else {
          count = parseInt(endMonth) - parseInt(startMonth)
        }
        for (let i = 0; i <= count; i++) {
          let tempYear = startDate.getFullYear().toString()
          let tempMonth = startDate.getMonth().toString().length === 1 ? '0' + startDate.getMonth().toString() : startDate.getMonth().toString()
          if (tempMonth === '00') {
            tempYear = (tempYear - 1).toString()
            tempMonth = '12'
          }
          _this.datePicker.push({'year': tempYear, 'month': tempMonth})
          startDate.setMonth(startDate.getMonth() + 1)
        }
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    }
  }
</script>
<style lang="less">
  .timePicket {
    width: 100%;
    height: 76px;
    color: #333;
    overflow: hidden;
    border-bottom: solid 1px #EEE;
    .outer-horizontal-scroll,
    .inner-horizontal-scroll {
      transform: rotate(0deg);
      position: relative;
      .cube-scroll-content {
        display: inline-block;
        .list-wrapper {
          white-space: nowrap;
          overflow: visible;
          //padding: 0 8px;
          padding: 14px 8px;
          .list-item {
            display: inline-block;
            width: 50px;
            vertical-align: top;
            dl {
              width: 100%;
              dt {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                line-height: 30px;
                font-size: 18px;
                margin: 0 auto;
                font-weight: 600
              }
              dd {
                font-size: 12px;
                line-height: 1;
                padding-top: 8px;
              }
            }
          }
          .list-item.active {
            dl {
              dt {
                background: #FF5A01;
                color: #fff;
              }
              dd {
                color: #FF5A01;
              }
            }
          }
        }
      }
    }
  }

  .page_topbar {
    color: #333;
    width: 100%;
    .plan_list {
      width: 100%;
      padding: 10px 15px;
      .title {
        width: 100%;
        height: auto;
        line-height: 16px;
        padding: 10px 0;
        font-size: 16px;
        font-weight: 600;
        text-align: left;
        letter-spacing: 1px;
        i {
          display: inline-block;
          width: 5px;
          height: 16px;
          border-radius: 2px;
          background: #FF5A01;
          vertical-align: -1px;
          margin-right: 10px;
        }
      }
      .sum {
        width: 100%;
        height: auto;
        font-size: 14px;
        padding: 10px 10px 12px;
        p {
          color: #666666;
          label {
            color: #FF5A01;
            font-size: 15px;
          }
          span {
            float: right;
            color: #333;
          }
        }
      }
      .plan_box {
        width: 100%;
        height: auto;
        padding: 21px 0;
        background: #fff;
        border-radius: 4px;
        box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.05);
        margin-bottom: 12px;
        dl {
          width: 100%;
          height: auto;
          h2 {
            line-height: 1;
            padding-bottom: 14px;
          }
          dt {
            width: 25%;
            float: left;
            border-right: solid 1px #EBEBEB;
            h2 {
              font-size: 20px;

            }
            p {
              text-align: center;
              font-size: 12px;
              color: #666666;
            }
          }
          dd {
            width: 75%;
            float: left;
            padding: 0 12px;
            h2 {
              font-size: 20px;
              color: #FF5A01;
              text-align: left;
              label {
                font-size: 12px;
                padding-left: 5px;
              }
            }
            p {
              font-size: 14px;
              label {
                float: right;
                letter-spacing: 1px;
              }
            }
          }
        }
      }
    }
  }
</style>



