<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll"
      :options="options"
      class="page_full bodyBg">
      <div class="card_list">
        <div class="card_box" v-for="(data,index) in items" :key="index">
          <div class="delete" @click="deleteCard(data.id)">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#shanchu"></use>
            </svg>
          </div>
          <dl>
            <dt>{{data.name}}</dt>
            <dd>{{data.oliCardNo}}</dd>
          </dl>
          <ul>
            <li>本月应到账：{{data.ydzMoney}}元</li>
            <li>累计充值金额：{{data.money}}元</li>
            <div class="clear"></div>
          </ul>
          <svg class="icon" aria-hidden="true">
            <use :xlink:href="data.oilType==='1'?'#zhongshiyou':'#zhongshihua'"></use>
          </svg>
        </div>
        <router-link to="/addTicket" tag="div" class="card_box add_card">
          <p><i>+</i>添加油卡</p>
        </router-link>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  import {postRequest} from '../axios'
  export default {
    name: 'my-card',
    data () {
      return {
        items: [],
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        },
        token: localStorage.getItem('hykToken')
      }
    },
    mounted () {
      this.loadPage()
    },
    methods: {
      deleteCard (id) {
        this.$createDialog({
          type: 'confirm',
          title: '',
          icon: 'cubeic-question',
          content: '是否移除该油卡？',
          confirmBtn: {
            text: '确定',
            active: true,
            disabled: false,
            href: 'javascript:;'
          },
          cancelBtn: {
            text: '取消',
            active: false,
            disabled: false,
            href: 'javascript:;'
          },
          onConfirm: () => {
            let _this = this
            postRequest(this.HOST + '/mine/delOilCard', {
              id: id,
              token: _this.token
            }).then(resp => {
              let data = resp.data
              if (data.code !== '200') {
                _this.showToast(data.msg)
                return false
              }
              _this.showConfirm('油卡已移除')
            })
          }
        }).show()
      },
      loadPage () {
        let _this = this
        postRequest(this.HOST + '/mine/oilCard', {
          token: _this.token
        }).then(resp => {
          let data = resp.data
          let list = data.list
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          list.forEach(function (item, i) {
            let oliCard = item.oliCardNo
            item.oliCardNo = oliCard.replace(/(.{4})/g, '$1 ')
          })
          _this.items = list
        })
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      },
      showConfirm (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'txt',
          time: 1500,
          onTimeout: () => {
            this.loadPage()
          }
        })
        toast.show()
      }
    }
  }
</script>
<style lang="less">
  .card_list {
    width: 100%;
    height: auto;
    padding: 15px 0 0 0 !important;
    color: #333;
    .card_box {
      width: 85%;
      height: 147px;
      border-radius: 4px;
      margin: 0 auto 20px auto;
      background: -webkit-linear-gradient(left top, #FF8D3D, #FF3548);
      background: -o-linear-gradient(bottom right, #FF8D3D, #FF3548);
      background: -moz-linear-gradient(bottom right, #FF8D3D, #FF3548);
      background: linear-gradient(to bottom right, #FF8D3D, #FF3548);
      box-shadow: 0 1px 8px rgba(213, 43, 43, 0.5);
      position: relative;
      overflow: hidden;
      .icon {
        width: 100px;
        height: 100px;
        position: absolute;
        bottom: -10px;
        right: -15px;
        fill: rgba(255, 255, 255, 0.2)
      }
      .delete {
        width: 40px;
        height: 40px;
        position: absolute;
        right: 3px;
        top: 3px;
        svg {
          width: 20px;
          height: 20px;
          position: absolute;
          top: 50%;
          left: 50%;
          margin: -10px 0 0 -10px;
          fill: #fff;
        }
      }
      ul {
        width: 100%;
        height: auto;
        background: rgba(0, 0, 0, 0.1);
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 9;
        li {
          float: left;
          width: 50%;
          font-size: 13px;
          color: #fff;
          padding: 15px 0;
          border-right: solid 1px rgba(255, 255, 255, 0.1)
        }
      }
      dl {
        width: 100%;
        padding: 20px;
        dt {
          color: #fff;
          text-align: left;
          font-size: 19px;
          line-height: 1;
          padding-bottom: 18px;
          letter-spacing: 2px;
        }
        dd {
          text-align: left;
          color: #fff;
          letter-spacing: 1.5px;
          font-size: 16px;
        }
      }
    }
    .card_box.add_card {
      p {
        text-align: center;
        display: inline-block;
        color: #fff;
        font-size: 18px;
        line-height: 135px;
        letter-spacing: 2px;
        i {
          font-style: normal;
          margin-right: 5px;
          font-size: 24px;
          vertical-align: -1px;
        }
      }
    }
  }

</style>



