<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll"
      :options="options"
      @pulling-down="onPullingDown"
      @pulling-up="onPullingUp"
      class="page_full bodyBg">
      <div class="notice_list">
        <router-link tag="div" to="/page" class="notice_box">
          <h2><span>2018-11-12</span>关于放假通知</h2>
          <p>反正就是放假反正就是放假反正就是放假反正就是放假反正就是放假...</p>
          <p class="more"><i></i>查看详情</p>
        </router-link>
        <div class="notice_box readed">
          <h2><span>2018-11-12</span>关于放假通知</h2>
          <p>反正就是放假反正就是放假反正就是放假反正就是放假反正就是放假...</p>
          <p class="more"><i></i>查看详情</p>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: 'notice-list',
    data () {
      return {
        options: {
          pullDownRefresh: {
            threshold: 60,
            txt: '刷新成功'
          },
          pullUpLoad: {
            threshold: 60,
            txt: {
              more: '加载更多',
              noMore: '没有更多了'
            }
          },
          scrollbar: {
            fade: true
          }
        }
      }
    },
    methods: {
      onPullingDown () {
        // 模拟更新数据
        setTimeout(() => {
          this.$refs.scroll.forceUpdate()
        }, 1000)
      },
      onPullingUp () {
        // 更新数据
        setTimeout(() => {
          this.$refs.scroll.forceUpdate()
        }, 1000)
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    }
  }
</script>
<style lang="less">
  .notice_list {
    width: 100%;
    height: auto;
    padding: 15px 0 0 0 !important;
    color: #333;
    .notice_box {
      width: 100%;
      height: auto;
      background: #fff;
      margin-bottom:12px;
      padding: 0 15px;
      h2 {
        font-size: 16px;
        width: 100%;
        padding-top: 10px;
        line-height: 32px;
        text-align: left;
        span {
          float: right;
          font-size: 12px;
          color: #999999;
        }
      }
      p {
        font-size: 13px;
        color: #666666;
        line-height: 1.5em;
        margin-bottom: 10px;
      }
      .more {
        border-top: solid 1px #ebebeb;
        padding: 10px 0;
        margin: 0;
        color: #999999;
        i {
          width: 15px;
          height: 13px;
          background: url("../../static/images/more_icon.png") no-repeat center center;
          background-size: contain;
          display: block;
          float: right;
        }
      }
    }
    .notice_box:last-child{
       margin:0;
    }
    .notice_box.readed{
       color:#999999;
       p{
         color:#999999;
       }
    }
  }
</style>



