<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll" :options="options" class="page_full bodyBg">
      <div class="page order_page">
        <div class="order_detail">
          <dl>
            <dt :style="{backgroundImage:`url(${hykOrder.goodsImg})`}">
            </dt>
            <dd>{{hykOrder.goodsName}}</dd>
          </dl>
          <ul>
            <li><span>{{hykOrder.cardNo}}</span>充值油卡号</li>
            <li><span>7.7折/12个月</span>充值套餐</li>
            <li><span>{{hykOrder.amt}}元</span>订单总额</li>
            <li v-if="hykOrder.redpackageAmt>0"><span>-{{hykOrder.redpackageAmt}}</span>优惠减免</li>
          </ul>
          <p><label>￥{{hykOrder.redpackageAmt>0?hykOrder.payMoney:hykOrder.amt}}</label>支付金额</p>
        </div>
        <div class="order_status">
          <ul>
            <li><span>{{hykOrder.orderNo}}</span>订单编号</li>
            <li><span>{{hykOrder.createDateStr}}</span>下单时间</li>
            <li v-if="hykOrder.orderStatus==='2' || hykOrder.orderStatus==='1'"><span>{{hykOrder.payDateStr}}</span>付款时间</li>
            <li class="status">
              <span class="success" v-if="hykOrder.orderStatus==='2'">已完成</span>
              <span v-else-if="hykOrder.orderStatus==='1'">充值中</span>
              <span class="wait" v-else-if="hykOrder.orderStatus==='0'">待支付</span>
              <span class="cancel" v-else-if="hykOrder.orderStatus==='4'">已失效</span>
              <span class="error" v-else>失败</span>
              订单状态</li>
          </ul>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  import {postRequest} from '../axios'
export default {
    name: 'order-detail',
    data () {
      return {
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        },
        hykOrder: [],
        orderNo: this.$route.query.orderNo,
        token: localStorage.getItem('hykToken')
      }
    },
    mounted () {
      this.loadOrder()
    },
    methods: {
      loadOrder () {
        let _this = this
        postRequest(this.HOST + '/order/seeOrder', {
          token: _this.token,
          orderNo: _this.orderNo
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          let list = data.hykOrder
          _this.$set(list, 'payMoney', (list.amt - list.redpackageAmt).toFixed(2))
          list.amt = list.amt.toFixed(2)
          _this.hykOrder = list
        })
      }
    }
  }
</script>
<style lang="less">
  .order_page {
    padding:0 !important;
    .order_status {
      width: 100%;
      height: auto;
      background: #fff;
      padding: 15px;
      ul {
        width: 100%;
        li.status {
          color: #333;
          font-size: 15px;
          span.wait {
            color: #FF5A01;
          }
          span.cancel {
            color: #999999;
          }
          span.success {
            color: #36A94C;
          }
          span.error{
            color: red;
          }
        }
        li {
          line-height: 1;
          padding: 9px 0;
          font-size: 14px;
          color: #999999;
          text-align: left;
          span {
            font-size: 15px;
            color: #333333;
            float: right;
          }
        }
      }
    }
    .order_detail {
      width: 100%;
      height: auto;
      background: #fff;
      margin-bottom: 13px;
      padding: 0 15px;
      p {
        font-size: 15px;
        line-height: 1;
        padding: 18px 0;
        label {
          color: #FF5A01;
          float: right;
        }
      }
      dl {
        width: 100%;
        height: auto;
        padding: 50px 0 30px 0;
        dt {
          width: 72px;
          height: 72px;
          border: solid 1px #ccc;
          border-radius: 2px;
          margin: 0 auto;
          background-size:cover;
          background-position: center center;
          background-repeat: no-repeat;
        }
        dd {
          padding-top: 18px;
          text-align: center;
          font-size: 15px;
        }
      }
      ul {
        width: 100%;
        padding-bottom: 10px;
        border-bottom: solid 1px #ebebeb;
        li {
          line-height: 1;
          padding: 9px 0;
          font-size: 14px;
          color: #999999;
          text-align: left;
          span {
            font-size: 15px;
            color: #333333;
            float: right;
          }
        }
      }
    }
  }
</style>




