<template>
    <cube-scroll
      ref="scroll"
      :options="options"
      :data="couponList"
      class="page_full bodyBg" style="z-index:999;">
      <div class="noDataTip coupon" v-if="couponList.length===0">
        <dl>
          <dt></dt>
          <dd>暂可用红包</dd>
        </dl>
      </div>
      <div class="coupon_list">
        <button @click="sendParent('')" v-if="couponList.length>0">不使用红包</button>
        <div class="coupon_box" v-for="(data,index) in couponList" :key="index" :class="data.status!=='0'?data.status==='1'? 'used':'over_time':''">
          <div class="coupon_info">
            <dl>
              <dt><span>￥</span>{{data.money}}</dt>
              <dd>
                <h2>{{data.title}}</h2>
                <p v-if="data.redType==='0'">仅限套餐充值使用</p>
                <p v-else-if="data.redType==='1'">仅限即时充值使用</p>
                <p v-else>无限制</p>
              </dd>
            </dl>
            <div class="use" v-if="data.status==='0'" @click="sendParent(data)">
              使用
            </div>
          </div>
          <div class="coupon_time">
            过期时间：{{data.overTimeStr}}
          </div>
        </div>
      </div>
    </cube-scroll>

</template>

<script>
  export default {
    name: 'coupon',
    data () {
      return {
        items: [],
        isLoadDom: false,
        token: localStorage.getItem('hykToken'),
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        }
      }
    },
    props: ['couponList'],
    methods: {
      sendParent (data) {
        this.$emit('listenChildEvent', data)
      }
    }
  }
</script>
<style lang="less" scoped>
  .bodyBg{
    animation: fadeInUpBig 0.8s;
    -webkit-animation: fadeInUpBig 0.8s;
  }
  @-webkit-keyframes fadeInUpBig {
    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, 2000px, 0);
      transform: translate3d(0, 2000px, 0);
    }

    100% {
      opacity: 1;
      -webkit-transform: none;
      transform: none;
    }
  }

  @keyframes fadeInUpBig {
    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, 2000px, 0);
      transform: translate3d(0, 2000px, 0);
    }

    100% {
      opacity: 1;
      -webkit-transform: none;
      transform: none;
    }
  }

  .fadeInUpBig {
    -webkit-animation-name: fadeInUpBig;
    animation-name: fadeInUpBig;
  }
  .coupon_list {
    width: 100%;
    height: auto;
    padding: 13px 13px 0 13px !important;
    button{width:100%;height:auto;background:#fff;box-shadow: 0 2px 3px #E8E8E8;border:none;border-radius:3px !important;font-size:15px;color:#333;padding:10px 0;margin-bottom:15px;outline: none;}
    .coupon_box {
      width: 100%;
      height: auto;
      margin-bottom: 10px;
      .coupon_info {
        width: 100%;
        height: 82px;
        padding: 0 20px;
        background: url("../../static/images/coupon_top_bg.png") no-repeat center center;
        background-size: cover;
        position: relative;
        overflow: hidden;
        .use {
          width: 60px;
          height: 25px;
          line-height: 24px;
          border-radius: 13px;
          border: solid 1px #fff;
          position: absolute;
          text-align: center;
          color: #fff;
          font-size: 14px;
          right: 20px;
          top: 50%;
          margin-top: -13px;
          a{
            color:#fff;
          }
        }
        dl {
          dt {
            color: #fff;
            font-size: 40px;
            float: left;
            line-height: 82px;
            span {
              font-size: 15px;
            }
          }
          dd {
            float: left;
            color: #fff;
            text-align: left;
            padding: 20px;
            h2 {
              font-size: 15px;
              font-weight: normal;
              line-height: 1;
              padding-bottom: 15px;
            }
            p {
              font-size: 12px;
              line-height: 1;
            }
          }
        }
      }
      .coupon_time {
        width: 100%;
        height: 43px;
        background: url("../../static/images/coupon_bottom_bg.png") no-repeat center center;
        background-size: cover;
        overflow: hidden;
        line-height: 43px;
        color: #999999;
        font-size: 12px;
        text-align: left;
        padding: 0 20px;
      }
    }
  }
</style>



