<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll" :options="options" class="page_full">
      <div class="page recharge_page">
        <div class="select_ticket">
          <p class="title">选择充值油卡</p>
          <div class="add_ticket" :class="{show:items.length<1}">
            <router-link to="/addTicket">
              <p><i>+</i>添加油卡</p>
            </router-link>
          </div>
          <div id="ticketList" v-if="items.length>0">
            <div class="swiper-container swiper-recharge">
              <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(data,index) in items" :key="index">
                  <div class="card_box">
                    <p>{{data.oilType==='1'?'中国石油':'中国石化'}}</p>
                    <dl>
                      <dt>{{data.name}}</dt>
                      <dd>{{data.oliCardNo}}</dd>
                    </dl>
                    <svg class="icon" aria-hidden="true">
                      <use :xlink:href="data.oilType==='1'?'#zhongshiyou':'#zhongshihua'"></use>
                    </svg>
                  </div>
                </div>
              </div>
              <div class="swiper-pagination"></div>
            </div>
          </div>
          <div class="shadow">
          </div>
        </div>
        <div class="month_money">
          <p>每月加油金额</p>
          <div class="select_money">
            <div class="button reduce" @click="handleReduce">
            </div>
            <div class="money" @click="handleInput">
              {{money}}<label>元</label>
            </div>
            <div class="button add" @click="handleAddmoney">
            </div>
          </div>
        </div>
        <div class="select_type">
          <p>选择充值套餐</p>
          <div class="type_list">
            <div class="box" v-for="(data,index) in list" :key="index" :class="data.id===selectId?'active':''"
                 @click="selectMeal(data.id,data.cycle,data.activityDiscount)">
              <dl>
                <div class="checked">
                  <svg class="icon" aria-hidden="true" fill="#fff">
                    <use xlink:href="#duigou"></use>
                  </svg>
                </div>
                <i class="flag hot" v-if="data.label==='1'"></i>
                <i class="flag limit" v-else-if="data.label==='0'"></i>
                <dt>{{data.Int}}<span>{{data.Last}}</span><label>折</label><s v-if="data.activityDiscount<data.discount">{{data.discount}}折</s>
                </dt>
                <dd>{{data.goodsName}}</dd>
              </dl>
            </div>
            <div class="clear"></div>
          </div>
        </div>
        <div class="recharge_info">
          <ul>
            <li><span>{{detail}}<label>{{totalMoney.toFixed(2)}}元</label></span>充值明细</li>
            <li @click="handleCoupon"><i class="more"></i><span :class="{usedCoupon:selectCoupon.length!==0}">{{selectCoupon.length===0?`有 ${count} 张可用优惠券`:`${selectCoupon.money}元 红包`}}</span>优惠券</li>
            <li @click="handleLayer"><i class="more"></i><span>查看计划</span>加油计划</li>
          </ul>
        </div>
        <div class="submit-button">
          <cube-button @click="handleRecharge">确定</cube-button>
          <p>首次充值完成后，2-48小时内到账</p>
        </div>
      </div>
    </cube-scroll>
    <use-coupon :couponList="coupon"  v-if="isCouponShow" v-on:listenChildEvent="getChildVal"></use-coupon>
    <div class="layer" v-if="isShow">
      <div class="dialog" :style="dialogCss">
        <div class="close" @click="handleClose">
          <div class="icon"></div>
        </div>
        <div class="title br">
          加油计划
        </div>
        <div class="content">
          <div class="layer_box">
            <cube-scroll
              ref="scroll_list"
              :options="options2"
              :data="plan"
              class="layer_list">
              <table>
                <tr>
                  <th>充值金额</th>
                  <th>到帐时间</th>
                </tr>
                <tr v-for="(data,index) in plan" :key="index">
                  <td>{{data.head}}期</td>
                  <td>{{data.foot}}</td>
                </tr>
              </table>
            </cube-scroll>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  import {postRequest} from '../axios'
  import useCoupon from './useCoupon'

  export default {
    name: 'recharge',
    data () {
      return {
        swiper: null,
        items: [],
        list: [],
        detail: '',
        selectId: this.$route.query.id,
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        },
        options2: {
          swipeBounceTime: 250
        },
        cardId: '',
        cardNo: '',
        token: localStorage.getItem('hykToken'),
        isShow: false,
        isCouponShow: false,
        money: 100,
        totalMoney: 100,
        dialogCss: {
          top: '17%',
          bottom: '17%'
        },
        selectCoupon: [],
        count: 0,
        zk: this.$route.query.zk,
        zq: this.$route.query.zq,
        plan: [],
        coupon: [],
        couponId: ''
      }
    },
    watch: {
      items (n) {
        if (n.length <= 1) {
          return false
        }
        this.$nextTick(function () {
          this.initSwiper()
        })
      },
      money (value) {
        let _this = this
        if (_this.zk !== undefined || _this.zq !== undefined) {
          _this.detail = `${value}*${(_this.zk * 10) / 100}*${_this.zq}=`
          _this.totalMoney = value * _this.zq * (_this.zk * 10) / 100
        } else {
          _this.totalMoney = value
        }
      }
    },
    mounted () {
      let _this = this
      _this.loadCard()
      _this.loadCoupon()
      if (_this.selectId === undefined) {
        _this.selectId = ''
      }
      if (_this.zk !== undefined || _this.zq !== undefined) {
        _this.detail = `${_this.money}*${(_this.zk * 10) / 100}*${_this.zq}=`
        _this.totalMoney = _this.money * _this.zq * (_this.zk * 10) / 100
      }
    },
    methods: {
      getChildVal (data) {
        let _this = this
        _this.selectCoupon = data
        _this.isCouponShow = false
        if (data.length !== 0) {
          _this.couponId = data.id
        }
      },
      selectMeal (id, zq, zk) {
        let _this = this
        _this.selectId = id
        _this.zq = zq
        _this.zk = zk
        _this.detail = `${_this.money}*${(zk * 10) / 100}*${zq}=`
        _this.totalMoney = _this.money * zq * (zk * 10) / 100
      },
      loadMeal () {
        let _this = this
        postRequest(this.HOST + '/details/goods', {
          token: _this.token,
          type: 0
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          let list = data.list1
          list.forEach(function (item, i) {
            let discount = item.activityDiscount.toString()
            if (discount.indexOf('.') !== -1) {
              let Int = discount.split('.')[0]
              let last = discount.split('.')[1]
              _this.$set(list[i], 'Int', Int)
              _this.$set(list[i], 'Last', `.${last}`)
            } else {
              _this.$set(list[i], 'Int', discount)
              _this.$set(list[i], 'Last', '')
            }
          })
          _this.list = list
          _this.count = data.count
        })
      },
      loadCard () {
        let _this = this
        postRequest(this.HOST + '/mine/oilCard', {
          token: _this.token
        }).then(resp => {
          let data = resp.data
          let list = data.list
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          list.forEach(function (item, i) {
            let oliCard = item.oliCardNo
            item.oliCardNo = oliCard.replace(/(.{4})/g, '$1 ')
          })
          _this.items = list
          _this.loadMeal()
        })
      },
      handleCoupon () {
        this.isCouponShow = true
      },
      loadCoupon () {
        let _this = this
        postRequest(this.HOST + '/mine/redPackage', {
          token: _this.token,
          redType: 0
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          let list = data.list
          list.forEach(function (item, i) {
            let overTimeStr = item.overTimeStr
            item.overTimeStr = _this.formatDate(overTimeStr, 'yyyy-mm-dd hh:MM:ss')
          })
          _this.coupon = list
        })
      },
      handleLayer () {
        let _this = this
        if (_this.selectId === '') {
          _this.showToast('请选择充值套餐')
          return false
        } else if (_this.money === 0) {
          _this.showToast('每月加油金额不正确')
          return false
        }
        postRequest(this.HOST + '/mine/oilManager', {
          cycel: _this.zq,
          stagesAmt: _this.money
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          _this.plan = data.hykOrder
          this.isShow = true
        })
      },
      formatDate (now, fmt) {
        let date = new Date(now * 1000)
        let o = {
          'M+': date.getMonth() + 1, // 月份
          'd+': date.getDate(), // 日
          'h+': date.getHours(), // 小时
          'm+': date.getMinutes(), // 分
          's+': date.getSeconds() // 秒
        }
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
        for (let k in o) { if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length))) }
        return fmt
      },
      handleClose () {
        this.isShow = false
      },
      handleReduce () {
        let _this = this
        if (_this.money >= 100) {
          _this.money -= 100
        } else {
          _this.money = 0
        }
      },
      handleAddmoney () {
        let _this = this
        _this.money += 100
      },
      handleRecharge () {
        let _this = this
        if (_this.selectId === '') {
          _this.showToast('请选择充值套餐')
          return false
        } else if (_this.money === 0) {
          _this.showToast('每月加油金额不正确')
          return false
        }
        postRequest(this.HOST + '/mine/packageRecharge', {
          token: _this.token,
          cardId: _this.cardId,
          cardNo: _this.cardNo,
          stagesAmt: _this.money,
          goodsId: _this.selectId,
          redpackageId: _this.couponId
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          _this.$router.replace('/pay?orderNo=' + data.orderNo)
        })
      },
      handleInput () {
        this.dialog = this.$createDialog({
          type: 'prompt',
          title: '自定义金额',
          prompt: {
            value: this.money,
            placeholder: '请输入加油金额',
            type: 'tel'
          },
          onConfirm: (e, promptValue) => {
            if (!(/(^[1-9]\d*$)/.test(promptValue))) {
              this.showToast('自定义金额不正确')
            } else {
              this.money = +promptValue
            }
          }
        }).show()
      },
      initSwiper () {
        let _this = this
        _this.swiper = new Swiper('.swiper-recharge', {
          watchSlidesProgress: true,
          slidesPerView: 'auto',
          centeredSlides: true,
          loop: true,
          onSlideChangeStart: function (swiper) {
            let index = swiper.realIndex
            _this.cardId = _this.items[index].id
            _this.cardNo = _this.items[index].oliCardNo.replace(/\s+/g, '')
          },
          onProgress: function (swiper, progress) {
            for (let i = 0; i < swiper.slides.length; i++) {
              let slide = swiper.slides.eq(i)
              let slideProgress = swiper.slides[i].progress
              let modify = 1
              if (Math.abs(slideProgress) > 1) {
                modify = (Math.abs(slideProgress) - 1) * 0.3 + 1
              }
              let translate = slideProgress * modify * 3 + 'px'
              let scale = 1 - Math.abs(slideProgress) / 7
              let zIndex = 999 - Math.abs(Math.round(10 * slideProgress))
              slide.transform('translateX(' + translate + ') scale(' + scale + ')')
              slide.css('zIndex', zIndex)
              slide.css('opacity', 1)
              if (Math.abs(slideProgress) > 2) {
                slide.css('opacity', 0)
              }
            }
          },
          onSetTransition: function (swiper, transition) {
            for (var i = 0; i < swiper.slides.length; i++) {
              var slide = swiper.slides.eq(i)
              slide.transition(transition)
            }
          }
        })
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    },
    components: {
      useCoupon
    }
  }
</script>
<style lang="less" src="../assets/recharege.less"></style>
<style lang="css" src="swiper/dist/css/swiper.min.css" scoped></style>



