<template>
  <div class="content_panel reg_panel">
    <cube-scroll
      ref="scroll" :options="options" class="page_full">
      <div class="page">
        <div class="reg_box pd">
          <input type="password" style="position:absolute;top:-50px;opacity:0;filter:alpha(opacity=0)"/>
          <div class="item">
            <cube-input type="password" placeholder="设置密码" :maxlength="16" :eye="eye" v-model="password"
                        class="setPass"></cube-input>
            <cube-input type="text" placeholder="推荐人邀请码（选填）" v-model="tjcode"></cube-input>
          </div>
          <div class="item">
            <cube-button @click="handleReg">确定</cube-button>
          </div>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  import {required, minLength} from 'vuelidate/lib/validators'
  import {onlyLetter, onlyNumber, hasBlank} from '../assets/js/validators'
  import {postRequest} from '../axios'
export default {
    name: 'setPass',
    data () {
      return {
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        },
        phone: this.$route.query.phone,
        code: this.$route.query.code,
        password: '',
        tjcode: '',
        eye: {
          open: false,
          reverse: false
        }
      }
    },
    validations: {
      password: {required, onlyLetter, onlyNumber, hasBlank, minLength: minLength(6)}
    },
    methods: {
      handleReg () {
        let _this = this
        if (!_this.checkPass()) {
          return false
        }
        postRequest(this.HOST + '/f/app/register', {
          phone: _this.phone,
          password: _this.password,
          inviterId: _this.tjcode
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          localStorage.setItem('hykUserId', data.id)
          localStorage.setItem('hykToken', data.token)
          localStorage.setItem('hykPhone', data.phone)
          _this.$router.replace('/home')
        })
      },
      checkPass () {
        let _this = this
        let verify = _this.$v
        verify.$touch()
        if (!verify.password.required) {
          _this.showToast('请输入密码')
          return false
        } else if (verify.password.onlyNumber) {
          _this.showToast('密码不能为纯数字')
          return false
        } else if (verify.password.onlyLetter) {
          _this.showToast('密码不能为纯字母')
          return false
        } else if (verify.password.hasBlank) {
          _this.showToast('密码不能包含空格')
          return false
        } else if (!verify.password.minLength) {
          _this.showToast('密码长度必须为6-16位')
          return false
        }
        return true
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    }
  }
</script>
<style lang="less" src="../assets/reg.less"></style>



