<template>
  <div class="content_panel reg_panel">
    <cube-scroll
      ref="scroll" :options="options" class="page_full">
      <div class="page">
        <div class="reg_box pd">
          <input type="password" style="position:absolute;top:-50px;opacity:0;filter:alpha(opacity=0)"/>
          <div class="item">
            <div class="view_phone">
              <p>{{labelPhone}}</p>
            </div>
            <cube-input type="password" :maxlength="16" :eye="eye" placeholder="登录密码" v-model="password"
                        class="pass"></cube-input>
            <div class="link">
              <p>
                <router-link to="/">找回密码</router-link>
                <router-link to="/userJoin">注册新账号</router-link>
              </p>
            </div>
          </div>
          <div class="item">
            <cube-button @click="handleNext">确定</cube-button>
          </div>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  import {required, minLength} from 'vuelidate/lib/validators'
  import {postRequest} from '../axios'

  export default {
    name: 'login',
    data () {
      return {
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        },
        phone: this.$route.query.phone,
        labelPhone: '',
        password: '',
        eye: {
          open: false,
          reverse: false
        }
      }
    },
    validations: {
      password: {required, minLength: minLength(6)}
    },
    mounted () {
      this.fomartPhone()
    },
    methods: {
      fomartPhone () {
        let str = this.phone
        this.labelPhone = str.substr(0, 3) + '****' + str.substr(7)
      },
      handleNext () {
        let _this = this
        if (!_this.checkPass()) {
          return false
        }
        postRequest(this.HOST + '/f/app/login', {
          phone: _this.phone,
          password: _this.password
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          localStorage.setItem('hykUserId', data.id)
          localStorage.setItem('hykToken', data.token)
          localStorage.setItem('hykPhone', data.phone)
          _this.$router.replace('/home')
        })
      },
      checkPass () {
        let _this = this
        let verify = _this.$v
        verify.$touch()
        if (!verify.password.required) {
          _this.showToast('请输入密码')
          return false
        } else if (!verify.password.minLength) {
          _this.showToast('密码长度必须为6-16位')
          return false
        }
        return true
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      }
    }
  }
</script>
<style lang="less" src="../assets/reg.less"></style>



