<template>
  <div class="content_panel index_panel">
    <cube-scroll
      ref="scrollpage"
      :options="options"
      :data="banner"
      class="page_scroll">
      <div class="page">
        <div class="banner">
          <div class="swiper-container banner_swiper">
            <div class="swiper-wrapper">
              <div class="swiper-slide" v-for="(data,index) in banner" :key="index">
                <a :href="data.url">
                  <div class="img bgimg_cover" :style="{backgroundImage:`url(${data.bannerImg})`}"></div>
                </a>
              </div>
            </div>
          </div>
          <div class="float">
            <div class="top">
            </div>
            <div class="tool_bar">
              <div class="icon">
                <dl class="safe">
                  <dt></dt>
                  <dd>充值保障</dd>
                </dl>
                <dl class="eq">
                  <dt></dt>
                  <dd>常见问题</dd>
                </dl>
                <dl class="caifly">
                  <dt></dt>
                  <dd>新手专享</dd>
                </dl>
                <div class="clear"></div>
              </div>
              <div class="notice">

              </div>
            </div>
          </div>
        </div>
        <div class="box">
        </div>
        <div class="product">
          <h2>套餐充值更划算</h2>
          <div class="product_list">
            <cube-scroll
              ref="scroll1"
              :data="items.list2"
              direction="horizontal"
              class="outer-horizontal-scroll">
              <ul class="list-wrapper">
                <li class="list-item" v-for="data in items.list2" :key="data.id">
                  <router-link tag="dl" :to="`/recharge?id=${data.id}&zk=${data.activityDiscount}&zq=${data.cycle}`">
                    <dt>{{data.Int}}<label>{{data.Last}}</label><span>折</span></dt>
                    <dd>
                      <p>{{data.goodsName}}</p>
                      <cube-button>立即充值</cube-button>
                    </dd>
                  </router-link>
                </li>
                <li class="list-item more">
                  <router-link tag="dl" to="/recharge">
                    <dt>
                    </dt>
                    <dd>查看更多</dd>
                  </router-link>
                </li>
              </ul>
            </cube-scroll>
          </div>
        </div>
        <div class="product">
          <h2>即时充值更快捷</h2>
          <div class="product_list">
            <cube-scroll
              ref="scroll2"
              :data="items.list1"
              direction="horizontal"
              class="outer-horizontal-scroll">
              <ul class="list-wrapper">
                <li class="list-item" v-for="data in items.list1" :key="data.id">
                  <router-link tag="dl" :to="`/recharge2?id=${data.id}`">
                    <dt>{{data.val}}<span>元</span></dt>
                    <dd>
                      <p class="price">售价<label>{{data.discountMoney}}</label>元</p>
                      <cube-button>立即充值</cube-button>
                    </dd>
                  </router-link>
                </li>
                <li class="list-item more">
                  <router-link tag="dl" to="/recharge2">
                    <dt>
                    </dt>
                    <dd>查看更多</dd>
                  </router-link>
                </li>
              </ul>
            </cube-scroll>
          </div>
        </div>
      </div>
    </cube-scroll>
    <footer-bar :sy="0"></footer-bar>
  </div>
</template>
<script>
import {postRequest} from '../axios/'
import footerBar from './footerBar'
import Swiper from 'swiper'
export default {
  name: 'index',
  data () {
    return {
      mySwiper: null,
      items: [],
      banner: [],
      options: {
        swipeBounceTime: 250,
        scrollbar: {
          fade: true
        }
      }
    }
  },
  mounted () {
    this.loadBanner()
  },
  watch: {
    banner (n) {
      if (n.length <= 1) {
        return false
      }
      this.$nextTick(function () {
        this.initSwiper()
      })
    }
  },
  methods: {
    loadBanner () {
      let _this = this
      postRequest(this.HOST + '/index/banner').then(resp => {
        let data = resp.data
        if (data.code !== '200') {
          _this.showToast(data.msg)
          return false
        }
        _this.banner = data.list
        _this.loadList()
      })
    },
    loadList () {
      let _this = this
      postRequest(this.HOST + '/index/goods').then(resp => {
        let data = resp.data
        if (data.code !== '200') {
          _this.showToast(data.msg)
          return false
        }
        let list = data.list2
        list.forEach(function (item, i) {
          let discount = item.activityDiscount.toString()
          if (discount.indexOf('.') !== -1) {
            let Int = discount.split('.')[0]
            let last = discount.split('.')[1]
            _this.$set(list[i], 'Int', Int)
            _this.$set(list[i], 'Last', `.${last}`)
          } else {
            _this.$set(list[i], 'Int', discount)
            _this.$set(list[i], 'Last', '')
          }
        })
        _this.items = data
      })
    },
    initSwiper () {
      this.mySwiper = new Swiper('.banner_swiper', {
        loop: false,
        autoplay: 5000
      })
    },
    showToast (tip) {
      const toast = this.$createToast({
        txt: tip,
        type: 'error',
        time: 1500
      })
      toast.show()
    }
  },
  components: {
    footerBar
  }
}
</script>
<style lang="less">
  .index_panel {
    .banner {
      width: 100%;
      position: relative;
      height: 200px;
      background:#f5f5f5;
      .swiper-container {
        width: 100%;
        height: 100%;
        .swiper-slide {
          height: 100%;
          .img {
            height: 100%;
            background-size:cover;
            background-repeat: no-repeat;
            background-position: center center;
          }
        }
      }
      a {
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
      }
      .float {
        width: 100%;
        height: auto;
        position: absolute;
        top: 175px;
        z-index: 99;
        padding: 0 16px;
        .cube-scroll-wrapper, .cube-scroll-list-wrapper {
          overflow: visible;
        }
        .top {
          width: 100%;
          background: #fff;
          border-radius: 3px 3px 0 0;
          height: 20px;
          position: relative;
        }
        .tool_bar {
          width: 100%;
          height: auto;
          padding: 1px 10px;
          background: #fff;
          box-shadow: 0 2px 15px #E8E8E8;
          margin-top: -1px;
          border-radius: 0 0 3px 3px;
          .icon {
            width: 100%;
            height: auto;
            border-bottom: solid 1px #EFEFEF;
            dl {
              width: 33.333%;
              height: auto;
              float: left;
              padding-bottom: 5px;
              dt {
                width: 50px;
                height: 50px;
                margin: 0 auto;
                border-radius: 50%;
              }
              dd {
                font-size: 13px;
                color: #666666;
                line-height: 1em;
                padding: 10px 0;
              }
            }
            .safe {
              dt {
                background: url("../../static/images/safe_icon.png") no-repeat center center;
                background-size: contain;
              }
            }
            .eq {
              dt {
                background: url("../../static/images/log_icon.png") no-repeat center center;
                background-size: contain;
              }
            }
            .caifly {
              dt {
                background: url("../../static/images/price_icon.png") no-repeat center center;
                background-size: contain;
              }
            }
          }
          .notice {
            width: 100%;
            height: 46px;
            padding: 12px 0;
            .cube-slide-item {
              width: 100%;
            }
            p {
              font-size: 14px;
              color: #333333;
              line-height: 20px;
              text-align: center;
              label {
                padding-right: 5px;
              }
            }
          }
        }
      }
    }

    .box {
      width: 100%;
      height: 130px;
    }

    .product {
      width: 100%;
      height: auto;
      overflow: hidden;
      padding: 8px 0;
      h2 {
        color: #333;
        font-size: 23px;
        text-align: left;
        line-height: 1;
        padding: 20px 16px 25px 16px;
      }
      .cube-scroll-list-wrapper,
      .outer-horizontal-scroll {
        overflow: visible;
      }
      .outer-horizontal-scroll,
      .inner-horizontal-scroll {
        transform: rotate(0deg);
        position: relative;
        .cube-scroll-content {
          display: inline-block;
          .list-wrapper {
            line-height: 60px;
            white-space: nowrap;
            overflow: visible;
            padding: 0 8px;
            .list-item {
              display: inline-block;
              width: 4.4rem;
              padding: 0 8px;
              vertical-align: top;
              dl {
                width: 100%;
                height: auto;
                background: #fff;
                padding: 22px 15px;
                box-shadow: 0 0 5px #E8E8E8;
                border-radius: 3px;
                dt {
                  font-size: 40px;
                  color: #F85948;
                  line-height: 1;
                  padding-bottom: 8px;
                  label {
                    font-size: 18px;
                  }
                  span {
                    font-size: 14px;
                    color: #666;
                    padding-left: 4px;
                  }
                }
                dd {
                  color: #333;
                  font-size: 12px;
                  p {
                    line-height: 1;
                    padding: 12px 0;
                    text-align: center;
                  }
                  p.price {
                    color: #999;
                    label {
                      color: #333;
                      padding: 0 5px;
                    }
                  }
                  button {
                    height: 30px;
                    line-height: 30px;
                    font-size: 14px;
                    padding: 0;
                    letter-spacing: 0.05rem;
                  }
                }
              }
            }
            .list-item.more {
              dl {
                padding: 37px 0;
                dt {
                  width: 42px;
                  height: 42px;
                  background: url("../../static/images/more.png") no-repeat center center;
                  background-size: contain;
                  margin: 0 auto;
                }
                dd {
                  font-size: 15px;
                  color: #666;
                  padding-top: 28px;
                  line-height: 1;
                }
              }
            }
          }
        }
      }
    }
  }
</style>
<style lang="css" src="swiper/dist/css/swiper.min.css" scoped></style>



