<template>
  <div class="content_panel">
    <cube-scroll
      ref="scroll" :options="options" class="page_full bodyBg page_set">
      <div class="page set_page">
        <div class="tool_list">
          <ul>
            <li><span>{{phoneTxt}}</span>手机号码</li>
            <!--<li><i class="more"></i><span>未绑定</span>微信</li>-->
            <li @click="selectSex"><i class="more"></i><span>{{sex==='0'?'保密':sex==='2'?'女':'男'}}</span>性别</li>
            <li @click="selectBirthday"><i class="more"></i><span>{{birthday}}</span>生日</li>
            <router-link to="/addressList" tag="li"><i class="more"></i>地址管理</router-link>
            <!--<router-link to="/invoiceLog" tag="li"><i class="more"></i>发票记录</router-link>-->
          </ul>
        </div>
        <div class="submit-button">
          <cube-button @click="handleOutlogin">安全退出</cube-button>
        </div>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  import {postRequest} from '../axios'

  const column1 = [{text: '保密', value: 0}, {text: '男', value: 1},
    {text: '女', value: 2}]
  export default {
    name: 'user-set',
    data () {
      return {
        phone: localStorage.getItem('hykPhone'),
        phoneTxt: '',
        sex: '0',
        birthday: '1992-1-1',
        options: {
          swipeBounceTime: 250,
          scrollbar: {
            fade: true
          }
        },
        token: localStorage.getItem('hykToken')
      }
    },
    mounted () {
      let _this = this
      _this.phoneTxt = _this.phone.substr(0, 3) + '****' + _this.phone.substr(7)
      this.loadPage()
    },
    methods: {
      loadPage () {
        let _this = this
        postRequest(this.HOST + '/mine/info', {
          token: _this.token
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          if (data.hykUser) {
            _this.sex = data.hykUser.sex
            _this.birthday = _this.formatDate(data.hykUser.registerDateStr, 'yyyy-MM-dd')
          }
        })
      },
      updataSex (sex) {
        let _this = this
        postRequest(this.HOST + '/mine/updateInfo', {
          token: _this.token,
          sex: sex
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          _this.loadPage()
        })
      },
      updataTime (date) {
        let _this = this
        postRequest(this.HOST + '/mine/updateInfo', {
          token: _this.token,
          birthday: date
        }).then(resp => {
          let data = resp.data
          if (data.code !== '200') {
            _this.showToast(data.msg)
            return false
          }
          _this.loadPage()
        })
      },
      handleOutlogin () {
        localStorage.removeItem('hykToken')
        localStorage.removeItem('hykPhone')
        localStorage.removeItem('hykUserId')
        this.$router.replace('/userJoin')
      },
      selectSex () {
        if (!this.picker) {
          this.picker = this.$createPicker({
            title: '请选择性别',
            data: [column1],
            onSelect: this.selectHandlesex
          })
        }
        this.picker.show()
      },
      selectBirthday () {
        if (!this.datePicker) {
          this.datePicker = this.$createDatePicker({
            title: '请选择生日',
            min: new Date(1900, 1, 1),
            max: new Date(2020, 9, 20),
            value: new Date(),
            onSelect: this.selectHandle
          })
        }
        this.datePicker.show()
      },
      selectHandlesex (selectedVal, selectedIndex, selectedText) {
        this.updataSex(selectedVal[0])
      },
      selectHandle (date, selectedVal, selectedText) {
        // let dateStr = selectedText[0] + '-' + selectedText[1] + '-' + selectedText[2]
        this.updataTime(date)
      },
      showToast (tip) {
        const toast = this.$createToast({
          txt: tip,
          type: 'error',
          time: 1500
        })
        toast.show()
      },
      formatDate (now, fmt) {
        let date = new Date(now * 1000)
        let o = {
          'M+': date.getMonth() + 1, // 月份
          'd+': date.getDate(), // 日
          'h+': date.getHours(), // 小时
          'm+': date.getMinutes(), // 分
          's+': date.getSeconds() // 秒
        }
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
        for (let k in o) { if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length))) }
        return fmt
      }
    }
  }
</script>

<style lang="less">
  .page_set {
    padding-top: 15px;
    .set_page {
      color: #333;
      .submit-button {
        padding: 20px 30px;
        button {
          letter-spacing: 4px;
        }
      }
      .tool_list {
        width: 100%;
        height: auto;
        background: #fff;
        padding: 0 15px;
        ul {
          width: 100%;
          margin-bottom: 30px;
          li {
            font-size: 14px;
            color: #666;
            line-height: 1;
            padding: 20px 1px;
            text-align: left;
            border-bottom: solid 1px #ebebeb;
            span {
              font-size: 15px;
              float: right;
              color: #333;
            }
          }
          li:last-child {
            border: none;
          }
          .more {
            width: 15px;
            height: 13px;
            background: url("../../static/images/more_icon.png") no-repeat center center;
            background-size: contain;
            display: block;
            float: right;
            margin-left: 5px;
          }
        }
      }
    }
  }
</style>



